#map{
    height: 30vw;
    width: 90vw;
    border-radius: 2vw;

    position: relative;
    left: 5%;
}

body{
    background-color: #1d1a1a;
}

.latlong-view{
    color:#f5eded;
}

.form-group{
    padding-top: 2vw;
    display: flex;
    align-items: center;
}

.form-group label{
    font-size: 2vw;
    color: #f5eded;
    padding-right: 1vw;

}

.form-group input{
    font-size: 1vw;
    background-color: #f5eded;
    border-radius: 1vw;
    border-width: 0.3vw;
    
    border-color: #d72323;
    width: 30vw;
}

.latlong-view{
    padding-top: 1vw;
}

.latlong-view p{
    font-size: 2vw;
}

.submitPart{
    display: flex;
}

.DiscordName{
    height: 3vw;
    width: 20vw;
    border-width: 0.3vw;
    border-radius: 1vw;
    font-size: 1vw;
    text-align: center;
    border-color: #d72323;
    
}

.AddToList{
    text-align: center;
    height: 3vw;
    width: 12vw;
    border-width: 0.3vw;
    border-radius: 1vw;
    border-color: #d72323;
}

.AddToList:hover{
    background-color: #c7c2c2;
}


